.welcomeText{
    font-family: 'Abel', sans-serif;
    text-align: center;
    margin-bottom: 0;
    font-size: 36px;
}

.scrambleText{
    font-family: 'Lato', sans-serif;
    text-align: center;
    font-size: 26px;
    color: #4f4f4f;
    margin-top: 10px;
    margin-bottom: 20px;
}

.profileImage{
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    height: auto;
    width: auto;
    max-width: 250px;
    max-height: 250px;
    -webkit-filter:brightness(120%);
            filter:brightness(120%);
    box-shadow: 0 10px 10px #b5b5b5;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
}

