.nameContainer{
    width: 100%;
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.navBar{
    float: right;
    padding-right: 26px;
    font-size: 26px;
}

.navBar li{
    display: inline;
    font-family: 'Abel', sans-serif;
    padding: 0 8px 0 8px;
}

.link:hover{
    color: #009dd1;
    cursor: pointer;
    letter-spacing: .02em;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
}

.link{
    text-decoration: none;
    color: #696969;
    -webkit-transition-duration: .2s;
            transition-duration: .2s;
}

.active{
     color: #000;
 }
.active:hover{
    color: #000;
}
.logo{
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: auto;
    max-width: 40px;
    max-height: 40px;
    fill: #696969;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.logo:hover{
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: auto;
    fill: #009dd1;
    cursor: pointer; /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2);
}

.anchor{
    height:30px;
    width:30px;
    margin: 0 6px 0 6px;
}

.container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.projectContainer{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    color: #696969;
}

.projectAttributes{
    justify-content: flex-start;
}

.projectTitle{
    font-family: 'Abel', sans-serif;
    font-size: 32px;
    margin: 0 0 0 0;
    padding: 10px 0 0 0;
    color: #009dd1;
}

.projectTechnologies{
    font-family: 'Lato', sans-serif;
    margin: 0 0 0 0;
    font-size: 18px;
    padding: 5px 0 15px 0;
}

.projectDate{
    font-family: 'Lato', sans-serif;
    margin: 0 0 0 0;
    font-size: 18px;
    padding: 10px 0 5px 0;
}


.projectImage{
    margin-left: auto;
    margin-right: auto;
    width: 700px;
    max-width: 700px;
    height: 500px;
    max-height: 500px;
    box-shadow: 0 10px 10px #b5b5b5;
    border-radius: 2%;
}

.projectDescription{
    font-family: 'Lato', sans-serif;
    padding: 10px 0 10px 0;
    font-size: 18px;
    max-width: 700px;
    width: 700px;
    color: #000000;
}
